<template>
  <v-container fluid>
    <v-flex xs12 sm6 offset-sm3>
      <v-select outline auto :loading="loading" :items="getYears" label="Select a year to see companies' launches" v-model="chosenYear" @input="makeChart"></v-select>
    </v-flex>
    <div v-if="launchesByAgency && !error">
      <p class="text-xs-left headline">Total: {{ totalLaunches }}</p>
      <PieChart :chartData="getPieChartData" />
    </div>
    <div v-if="launches">
      <LineChart :chartData="getLineChartData" />
    </div>
    <div v-if="agenciesReady">
      <RadarChart :chartData="getRadarChartData" />
    </div>
    <h3 v-if="error">
      <v-chip>
        <v-avatar class="red">
          <v-icon>close</v-icon>
        </v-avatar>
        No information about launches in {{ chosenYear }}
      </v-chip>
    </h3>
  </v-container>
</template>
<script>
import PieChart from './PieChart'
import LineChart from './LineChart'
import RadarChart from './RadarChart'

export default {
  data () {
    return {
      chosenYear: new Date().getFullYear(),
      launches: null,
      agencies: null,
      launchesByAgency: null,
      loading: false,
      error: false,
      agenciesReady: false
    }
  },
  computed: {
    getYears () {
      const years = []
      for (let i = 1980; i < new Date().getFullYear() + 12; i++) {
        years.push(i)
      }
      return years
    },
    totalLaunches () {
      return this.launches ? this.launches.length : 0
    },
    getPieChartData () {
      return {
        labels: this.createLabels,
        datasets: [
          {
            backgroundColor: this.getColors(),
            borderWidth: 0,
            data: Object.values(this.launchesByAgency)
          }
        ]
      }
    },
    getLineChartData () {
      const color = this.randomColor()
      const alphaColor = color.slice(0, 3) + 'a' + color.slice(3, -1) + ',0.5)'
      return {
        labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
        datasets: [
          {
            label: 'Launches',
            data: this.createLineData,
            borderColor: color,
            fill: true,
            fillOpacity: 0.5,
            backgroundColor: alphaColor,
            pointBorderWidth: 5
          }
        ]
      }
    },
    getRadarChartData () {
      const color = this.randomColor()
      const alphaColor = color.slice(0, 3) + 'a' + color.slice(3, -1) + ',0.5)'
      return {
        labels: ['Europe', 'Asia', 'North America', 'Africa', 'South America', 'Oceania'],
        datasets: [
          {
            label: 'Continent',
            fill: true,
            backgroundColor: alphaColor,
            borderColor: color,
            data: this.createRadarData,
            pointRadius: 5,
            pointBackgroundColor: color
          }
        ]
      }
    },
    createLabels () {
      const labels = []
      for (let prop in this.launchesByAgency) {
        labels.push(`${prop} – ${this.launchesByAgency[prop]}`)
      }
      return labels
    },
    createLineData () {
      const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
      return months.map(month => {
        return this.launches.filter(item => item.net.includes(month)).length
      })
    },
    createRadarData () {
      const continents = ['Europe', 'Asia', 'North America', 'Africa', 'South America', 'Oceania']
      return continents.map(continent => {
        return this.launches.filter(item => {
          return this.agencies[item.lsp] && this.agencies[item.lsp].continent === continent
        }).length
      })
    }
  },
  created () {
    this.makeChart()
  },
  methods: {
    makeChart () {
      this.$Progress.start()
      this.error = false
      this.$http.get(`https://launchlibrary.net/1.4/launch?startdate=${this.chosenYear}-01-01&enddate=${this.chosenYear}-12-31&limit=-1`)
        .then(response => {
          this.launches = response.body.launches
          if (this.$store.state.agencies) {
            this.getAgencies()
          } else {
            this.$store.dispatch('getAgenciesInfo')
              .then(() => {
                this.getAgencies()
              })
          }
        }, response => {
          this.launches = null
          this.error = true
          console.log(new Error(response.body.msg))
          this.$Progress.fail()
        })
    },
    getAgencies () {
      this.agencies = {}
      this.$store.state.agencies.map(item => {
        this.agencies[item.id] = {
          name: item.name,
          continent: item.continent
        }
      })
      this.agenciesReady = true
      this.getAgencyLaunches()
      this.loading = false
    },
    getAgencyLaunches () {
      const unique = [...new Set(this.launches.map(item => item.lsp))]
      this.launchesByAgency = {}
      let launchesForSort = []
      unique.map(item => {
        launchesForSort.push({ name: this.agencies[item] ? this.agencies[item].name : 'unknown', amount: this.calcAgencyCount(item) })
      })
      launchesForSort.sort((a, b) => {
        return b.amount - a.amount
      })
      launchesForSort.map(item => {
        this.launchesByAgency[item.name] = item.amount
      })
      this.$Progress.finish()
    },
    calcAgencyCount (lsp) {
      return this.launches.length > 0 ? this.launches.filter(item => {
        return item.lsp === lsp
      }).length : 0
    },
    getColors () {
      const colors = []
      for (let i = 0; i < Object.keys(this.launchesByAgency).length; i++) {
        colors.push(this.randomColor())
      }
      return colors
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    }
  },
  components: {
    PieChart,
    LineChart,
    RadarChart
  }
}
</script>



